import React, { Component } from "react";

class HistorySuggestItem extends Component {
  handleClick(item) {
    //  console.log(this.props)
    let actionItem = {};
    actionItem.item = item;
    this.props.fatchResultForHistory(actionItem);
    //console.log(item);
  }
  removeHistoryItem = (hitem) => {
     this.props.removeItemFromHistory(hitem);
  }
  render() {
    return (
        <div className="historyitem">
            <div
                onClick={this.handleClick.bind(this, this.props.historyitem)}>
                     {this.props.historyitem}
            </div>
             <span className="badge badge-warning"
                    onClick={this.removeHistoryItem.bind(this, this.props.historyitem)}
            >x</span>
        </div>    
    );
  }
}

export default HistorySuggestItem;



