import React, { Component } from "react";
import VoiceIconOverlay from './voiceIconOverlay/voiceIconOverlay'
import "./voiceSearch.css";


class VoiceSearch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      noSupport: true,
      isSupport: true,
      disabledButton:false,
      textToSpeak:""
    };    
    this.startDictation = this.startDictation.bind(this)
  }
  componentWillReceiveProps(props){ 
    console.log(props.stringToRead);
    this.setState({textToSpeak:props.stringToRead})
   }
  componentDidMount(){   
    
    if (!(window.hasOwnProperty('webkitSpeechRecognition'))) {
      this.setState({ noSupport: false });
      this.setState({ isSupport: false });   
    }
   
    setTimeout(() => {
      this.setState({ noSupport: true }); 
    }, 8000)
  }
  startDictation() {
    console.log("one");
    var diagnostic = document.querySelector('#voiceErrorMsg');
    if (window.hasOwnProperty('webkitSpeechRecognition')) {
     console.log("two");
      
      var recognition = new (window.SpeechRecognition || window.webkitSpeechRecognition || window.mozSpeechRecognition || window.msSpeechRecognition)();
      var SpeechGrammarList = window.SpeechGrammarList || window.webkitSpeechGrammarList;
      var SpeechRecognitionEvent = window.SpeechRecognitionEvent || window.webkitSpeechRecognitionEvent;
      var speechRecognitionList = new SpeechGrammarList();
      recognition.grammars = speechRecognitionList;
      //recognition.continuous = false;
      recognition.lang = 'en-In';
      recognition.interimResults = false;
      recognition.maxAlternatives = 1;

      this.setState({ isLoading: true, disabledButton:true });
      recognition.start();
      console.log('Ready to receive a color command.');
      diagnostic.textContent = 'Speak Now...';
      var getVoice;
     recognition.onresult = function(event) {
        var last = event.results.length - 1;
        var searchText = event.results[last][0].transcript; 
        let valueis = searchText, getVoice = searchText;	
        this.setState({ isLoading: false, disabledButton:false });			
        recognition.stop();      
        this.props.onVoiceSearchInitiated(valueis);
        console.log('Confidence: ' + event.results[0][0].confidence);       
      }.bind(this);
      
      recognition.onspeechend = function(event) {
        recognition.stop();  
        console.log(event);        
        diagnostic.textContent = 'You speech stops';       
        setTimeout(() => {
          this.setState({ isLoading: false, disabledButton:false });    
        }, 4000)
      }.bind(this);

      recognition.onerror = function(event) {
        diagnostic.textContent = 'Error occurred in recognition: ' + event.error + ' Try Again';
        setTimeout(() => {
          this.setState({ isLoading: false, disabledButton:false });    
        }, 3000)
      }.bind(this);
      
      recognition.onnomatch = function(event) {
        diagnostic.textContent = 'I didnt recognise your voice.';
      }
      setTimeout(() => {
        this.setState({ isLoading: false, disabledButton:false });    
      }, 20000)
    }
  }

  speakText(){
    if(this.state.textToSpeak !== ""){
      window.speak(this.state.textToSpeak);
    }    
  }

  render() {
    return (<div>
      <div
      className={
              this.state.noSupport
                ? "hidden"
                : ""
            }
      >
          <div className="isa_warning">Sorry, your browser not support voice based search, please swith to Chrome</div>
       </div>
       <div
            className={
              this.state.isLoading
                ? "pos-top"
                : "hidden"
            }
          >
          <VoiceIconOverlay></VoiceIconOverlay>
       </div>
      
      <button 
      className={
              this.state.isSupport
                ? "fa fa-microphone get-voice"
                : "hidden"
            }
       onClick={this.startDictation.bind(this)} disabled={this.state.disabledButton}></button> 
      <div className={this.state.textToSpeak !== ""? "voice-icon":"hidden"}>
      <button onClick={this.speakText.bind(this)} className="fa fa-volume-up get-voice" id="vs"></button>
      <div id="audio"></div>    
      </div>
      </div>
    );
  }
}

export default VoiceSearch;





