import React from 'react';
import Autosuggest from 'react-autosuggest';
import Parse from 'autosuggest-highlight/parse';
import Match from 'autosuggest-highlight/match';
import HistorySuggestItem from './HistorySuggestItem';
import './Autosuggest.css';

import $ from 'jquery';



var products;
// https://developer.mozilla.org/en/docs/Web/JavaScript/Guide/Regular_Expressions#Using_Special_Characters
function escapeRegexCharacters(str) {
  return str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
}

function getSuggestions(value, suggestion) {
  if(products){
    const escapedValue = escapeRegexCharacters(value.trim());
    
      if (escapedValue === '') {
        return [];
      }
    
      const regex = new RegExp('\\b' + escapedValue, 'i');
    
      return products.filter(person => regex.test(getSuggestionValue(person)));
  }else{
    console.log('No data in product');
  }
  
}

function getSuggestionValue(suggestion) {
  //console.log(`${suggestion.title}`);
  return `${suggestion.title}`;
}

function renderSuggestion(suggestion, { query }) {
  const suggestionText = `${suggestion.title}`;
  const matches = Match(suggestionText, query);
  const parts = Parse(suggestionText, matches);

  return (
    <span className={'suggestion-content ' + suggestion.category}>
      <span className="name">
      <span className="suggItemTitle">
        {
          parts.map((part, index) => {
            const className = part.highlight ? 'highlight' : null;

            return (
              <span className={className} key={index}>{part.text}</span>
            );
          })
        } 
        </span>
        <span className="inCategory"> <strong></strong></span>
        <span className="priceSpan">{suggestion.currency} {suggestion.price}</span>
        <span className="sugg-cart"><i className="fa fa-shopping-cart"></i> Cart</span>
      </span>
    </span>
  );
}

class CutstomAutoSuggest extends React.Component {
  
  constructor(props) {
    super(props);

    this.state = {
      value: '',
      suggestions: [],
      onTypeText:true,
      showHistorySugget:false,
      onPageLoadState:true
    };
   this.clearSearchText = this.clearSearchText.bind(this);  
   this.pageClick = this.pageClick.bind(this);
  }
 
  componentDidMount(){
   
    /* trigger event on component load*/
         
    window.addEventListener('mouseup', this.pageClick, false);
  }
  pageClick(e){    
  /*  if(e.target.parentNode.offsetParent.id !== 'historySearchBox'){
      this.setState({showHistorySugget:false});
    }*/
  }
  componentWillReceiveProps(props){  
    var self = this;
    if(props.productsHead && props.productsHead.length > 0 && this.state.onPageLoadState === true){
       var snsData = props.productsHead.map(function(p , i){
         var newObj = {};
         newObj.title = p._source.title;
         newObj.category = p._source.category;
         newObj.price = p._source.price;
         newObj.currency = p._source.currency;
       
         return newObj;
         })
         
         products = snsData;
         self.setState({
           suggestions: snsData,
           onPageLoadState:false
         });
    }else{
      console.log('No data in prop head');
    }
    if(props.suggesthistoryappheader === ""  || props.suggesthistoryappheader.length === 0){
      this.setState({showHistorySugget:false})
    }
    if(props.currentQueryApp !== this.state.value){
      this.setState({value:props.currentQueryApp})
    }
  }
  onChange = (event, { newValue, method }) => {
   console.log(newValue);
    this.setState({
      value: newValue,
      onTypeText: false
    });  
    if(newValue)  { 
      this.setState({showHistorySugget:false})  
    }else{
      this.setState({showHistorySugget:true})  
    }
    console.log(this.state.suggestions);
  };
  searchData = (event) =>{
    this.props.onItemSelected(event,{enteredValue:"", suggestion:{title: this.state.value}} );    
  }
  clearSearchText = () => {
    this.setState({
      value: ""
    });
    this.props.headerclearsearch();
  }
  onSuggestionsFetchRequested = ({ value }) => {
    var suggestion = this.state.suggestions;
    this.setState({
      suggestions: getSuggestions(value, suggestion)
    });
  };

  onSuggestionsClearRequested = () => {
    this.setState({
      suggestions: []
    });

  };
  enterPressed = (event) => {
      var code = event.keyCode || event.which;
      if(code === 13) { //13 is the enter keycode
        this.props.onItemSelected(event,{enteredValue:"", suggestion:{title: this.state.value}} ); 
      }
      this.setState({showHistorySugget:false})    
  }
  customsugget = (event) => { console.log(this.state.value);
    if((this.state.onTypeText || this.state.value === "") && this.props.suggesthistoryappheader.length > 0){
      this.setState({showHistorySugget:true}); 
    }else{
      this.setState({showHistorySugget:false})
    }
    
  }
  onItemSelected = (event, data) =>{
    data.enteredValue = this.state.value;
    this.props.onItemSelected(event, data);    
  };
  render() {
    let searchboxvalue = this.props.searchboxvalueonheader,
    historyOption;

    
    const { value, suggestions } = this.state;
    const inputProps = {
      placeholder: "Search for Products, Brands, Categories and more",
      value,
      onChange: this.onChange,
      className: "form-control search-box",
      id:"autoSearchBox",
      onKeyPress: this.enterPressed,
      onClick: this.customsugget
    };
    
    if(this.props.suggesthistoryappheader.length > 0){
      historyOption = this.props.suggesthistoryappheader.map((item, index) => {
        return <HistorySuggestItem key={index} historyitem={item} 
        fatchResultForHistory={this.props.fatchResultForHistory}
        removeItemFromHistory={this.props.removeItemFromHistory}/>
      })
    }
    
    return (<div className="m-as">
      <Autosuggest
        suggestions={suggestions}
        onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
        onSuggestionsClearRequested={this.onSuggestionsClearRequested}
        getSuggestionValue={getSuggestionValue}
        renderSuggestion={renderSuggestion}
        inputProps={inputProps}
        onSuggestionSelected={this.onItemSelected}
        />
        <i className="fa fa-search s-search" onClick={this.searchData.bind(this)}></i>
        <i  className={
              this.state.value
                ? "fa fa-times fa-1 clear-s-text"
                : "hidden"
            }
            onClick={
              this.clearSearchText.bind(this)
            }></i>   
            <div className={this.state.showHistorySugget ? "display-dalalist": "hidden"} id="historySearchBox">
              <div className="historyItemList">
               {historyOption}
             </div>
             <div className="clearHistory"
                 onClick={this.props.clearHistoryData}>
                Clear History
             </div>
            </div>       
      </div>
    );
  }
}

export default CutstomAutoSuggest;



