import React, { Component } from "react";

class SubItem extends Component {
  constructor() {
    super();
    this.state = {
      filterState: false,
      tempState: true
    };
    this.onSubmitCategory = this.onSubmitCategory.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    
        if(nextProps.itemToReset.item !== undefined && nextProps.itemToReset.checkState !== undefined) {
           console.log("new title is", nextProps.itemToReset.item);
           if(nextProps.itemToReset.item === this.props.filterSetData.key)
           this.setState({
            filterState: nextProps.itemToReset.checkState            
           })
        }
    }
  onSubmitCategory() {
    //  console.log(this.props)
    let actionItem = {};
    this.setState({ filterState: !this.state.filterState }, function() {
      if (this.state.filterState) {
        actionItem.to = "add";
      }
      actionItem.item = this.props.filterSetData.key;
      this.props.getCategoryItemfromFP(actionItem);
    });

    //console.log(item);
  }

  render() {
    return (
      <li>
        <label className="custom-control custom-checkbox">
          <input
            name={this.props.filterSetData.key}
            className="custom-control-input"
            type="checkbox"
            checked={this.state.filterState}
            onChange={this.onSubmitCategory.bind(this)}
          />
          <span className="custom-control-indicator" />
          <span
            className="custom-control-description"
            id={this.props.filterSetData.doc_count}
          >
            {this.props.filterSetData.key} ({this.props.filterSetData.doc_count})
          </span>
        </label>
      </li>
    );
  }
}

export default SubItem;



