import React, { Component } from 'react';
import SubItem from './SubItem';

class FilterPanel extends Component{
  constructor(){
    super();
    this.state = {
      condition: false,
      categoryLabel: false,
      filterState: false
    }
    this.handleClickEvent = this.handleClickEvent.bind(this);
  }


    componentWillMount(){
      if(this.props.categoryLabel === 'categories'){
        this.setState( { condition :  !this.state.condition } );
      }
    }
    handleClickEvent(event){
      this.setState( { condition : !this.state.condition } );
    }
    onSubmitCategory(){
    //  console.log(this.props)
        let actionItem={};
        this.setState({filterState: !this.state.filterState},function(){
          if(this.props.filterState){
            actionItem.to = 'add'
          }
          actionItem.item = this.props.categoryLabel;
          this.props.getCategoryItemfromFP(actionItem);
        })

      //console.log(item);
    }
  render(){
  
      var newArray = this.props.leftNavDataSet, subItems; 
      
       subItems = newArray.map((item, index, items) =>{                 //console.log("items ", items, item);
              
                   return <SubItem filterSetData={item} key={index}
                   filterState={this.state.filterState}
                   getCategoryItemfromFP={this.props.getCategoryItemLN}
                   itemToReset={this.props.itemToReset}
                   filterCheckStatusForSI={this.props.filterCheckNstatusForFP}
                   />
              
    
       })
    
    
  
    return (
      <div className="panel panel-default">
        <div className="panel-heading">
          <h4 className="panel-title">
            <a data-toggle="collapse" onClick={this.handleClickEvent.bind(this)} className={this.state.condition ? 'acc-open' : 'acc-close'} data-parent="#accordian" href={'#'+this.props.forAccordian}>
              <span className="badge pull-right"><i className="fa fa-plus"></i></span>
              {this.props.categoryLabel}
            </a>
          </h4>
        </div>
        <div id={this.props.forAccordian} className={this.state.condition ? 'panel-collapse collapse in':'panel-collapse collapse'} >
          <div className="panel-body">
            <ul>
               {subItems}
            </ul>
          </div>
        </div>
      </div>

    )
  }
}

export default FilterPanel;



