import React, { Component } from 'react';
import $ from "jquery";

class ProductItem extends Component {
  addDefaultSrc(ev){
    ev.target.src = 'images/404/404.png'
  }
  replaceStr(str){      
   // return str.split('images/productimages/source').join('Images/ProductImages/Source');
  }
  render() {
    var color = this.props.productDetails._source.colour,
    itemDescription = [];
    if(color !== undefined){
      var colorArray = color.split(',');
      colorArray = colorArray.map((item, index) =>{
            return <span key={item} style={{backgroundColor:item}}></span>
      })
    }
  /*  if(this.props.productDetails._source.length > 0){
     
      let pDetail = this.props.productDetails._source;
       for(let i of pDetail){
            itemDescription.push(i);
        }
        itemDescription = itemDescription.map((item, index) =>{
          if(index > 1 && index < 15){
            return <li key={index}>{item}</li>
          }         
        })
    }*/

    return (
						<div className="col-sm-12">
							<div className="product-image-wrapper">
								<div className="col-sm-2"><div className="sns-pimg"><a href={this.props.productDetails._source.url}><img src={this.props.productDetails._source.imageurl} alt="" onError={this.addDefaultSrc} /></a></div></div>
								<div className="col-sm-7"><h4>{this.props.productDetails._source.title}</h4>
								  <ul className="nav p-details">
                  {colorArray &&
                      <li><span>Colour:</span> <span className="color-available">{colorArray}</span></li>
                  }
                  {this.props.productDetails._source.color &&
                      <li><span>Colour:</span> <span className="color-available"><span style={{backgroundColor:this.props.productDetails._source.color}}></span></span></li>
                  }
                  {this.props.productDetails._source.Material &&
                    <li><span>Material:</span> {this.props.productDetails._source.material}</li>
                  }
                  {this.props.productDetails._source.Neckline &&
                    <li><span>Neckline:</span> {this.props.productDetails._source.neck}</li>
                  }
                  {this.props.productDetails._source.pattern &&
                    <li><span>Fabric:</span> {this.props.productDetails._source.fabric}</li>
                  }
                   {this.props.productDetails._source.sleeves &&
                    <li><span>Sleeves:</span> {this.props.productDetails._source.sleeves}</li>
                  }
                  {this.props.productDetails._source.craft &&
                    <li><span>Craft:</span> {this.props.productDetails._source.craft}</li>
                  }
                  {this.props.productDetails._source.fit &&
                    <li><span>Fit:</span> {this.props.productDetails._source.fit}</li>
                  }
                   {this.props.productDetails._source.model &&
                    <li>{this.props.productDetails._source.model}</li>
                  }
                 {this.props.productDetails._source.battery &&
                    <li>{this.props.productDetails._source.battery} mAh <span>Battery</span></li>
                  }
                  {this.props.productDetails._source.display &&
                    <li>{this.props.productDetails._source.display} inch <span>Screen display</span></li>
                  }
                   {this.props.productDetails._source.ram &&
                    <li>{this.props.productDetails._source.ram} GB <span>Ram</span></li>
                  }
                   {this.props.productDetails._source.internal_memory &&
                    <li>{this.props.productDetails._source.internal_memory} GB <span>Internal memory</span></li>
                  }
                  {this.props.productDetails._source.primary_camera &&
                    <li>{this.props.productDetails._source.primary_camera} MP <span>Primary camera</span></li>
                  }                 
                   {this.props.productDetails._source.secondary_camera &&
                    <li>{this.props.productDetails._source.secondary_camera} MP <span>Secondary camera</span></li>
                  }
                              
                
                  
								  </ul>
								</div>
								<div className="col-sm-3">
								  <h3>{this.props.productDetails._source.currency} {this.props.productDetails._source.price}</h3>
								  <div className="choose">
										<ul className="nav nav-pills nav-justified">
											<li><a href="">Category: {this.props.productDetails._source.category}</a>
												<a href="">Brand: {this.props.productDetails._source.brand}</a>
											</li>
										</ul>
									</div>
								</div>
							</div>
						</div>

    );
  }
}

export default ProductItem;



