import React, { Component } from 'react';
import FilterPanel from './FilterPanel';
import PriceRangeSliderComponent from './PriceRangeSlider';
import FilterTag from './FilterTag';
import PortalComponent from './PortalComponent';
import './leftNav.css';


let currentFilter=[]
class LeftNav extends Component {


  constructor(props){
      super(props);
      this.state = {
        lnData : [],
        displayedCategoriessns: [],
        setSubItemState:[{defaultstate:false}]
      }
      console.log(this.props.priceRangeSlider);
  }
  
  componentWillMount(){
   
  }
  updateStateOfSelection = (item, method) => {
    if(method === 'update'){
     let tempObj = {};
     tempObj.item = item;
     tempObj.state = true
    }else{

    }
  }
  render() {
     console.log(this.props.filterCheckStatusForLN);
    let currentFilter = [], facets = [];
    if(this.props.selectedFilters.length > 0){
      facets = this.props.selectedFilters.map((item, index) =>{
        return <FilterTag setFilterItem={item}
                          key={index}
                          getCategoryItemfromFP={this.props.getFilterItemsFromFacets} />
      })
    }
    if(this.props.showLeftNave.constructor === Object){

      let shortedList = this.props.showLeftNave;      
       for(var k in shortedList){
             let currentArray = shortedList[k].buckets,tempComponent;
             console.log(currentArray);
             console.log(k);
             tempComponent =
               <FilterPanel leftNavDataSet={currentArray}
               categoryLabel={k}
               key={k}
               forAccordian={k}
               selectionFlag={this.state.setSubItemState}
               getCategoryItemLN={this.props.getCategoryItemApp}
               itemToReset={this.props.removeFilterInLN}
               filterCheckNstatusForFP={this.props.filterCheckStatusForLN}
               />               
               currentFilter.push(tempComponent)   

        }
      
    }

    return (
           <div className="col-sm-3">
             <div className="left-sidebar">
               <h2>Filters</h2>
               <div className="facet-container"><PortalComponent targetId={'facet-container'} >{facets}</PortalComponent></div>
               <div className="panel-group category-products" id="accordian">
                   <div className={this.props.visibleLoader ? "timeline-wrapper" :"timeline-wrapper hidden"}>
                         <div className="timeline-item">
                            <div className="animated-background facebook">
                                <div className="background-masker header-top"></div>
                                <div className="background-masker header-left"></div>
                                <div className="background-masker header-bottom"></div>
                                <div className="background-masker subheader-left"></div>
                                <div className="background-masker subheader-right"></div>
                                <div className="background-masker subheader-bottom"></div>
                                <div className="background-masker content-top"></div>
                                <div className="background-masker content-first-end"></div>
                                <div className="background-masker content-second-line"></div>
                                <div className="background-masker content-third-line"></div>
                            </div>
                         </div>
                     </div>
                    {currentFilter}
               </div>
                 <div className={this.props.visibleLoader ? "hidden" :""}>
                 <PriceRangeSliderComponent priceRangeSliderRender={this.props.priceRangeSlider} />
                 </div>
             </div>
           </div>
    );
  }
}

export default LeftNav;



