import React, { Component } from "react";

class FilterTag extends Component {
  handleClick(item) {
    //  console.log(this.props)
    let actionItem = {};
    actionItem.item = item;
    this.props.getCategoryItemfromFP(actionItem);
    //console.log(item);
  }

  render() {
    return (
      <button
        className="btn btn-outline-dark btn-sm"
        onClick={this.handleClick.bind(this, this.props.setFilterItem)}
      >
        {this.props.setFilterItem}{" "}
        <span className="badge badge-warning">x</span>
      </button>
    );
  }
}

export default FilterTag;



