import React from "react";
import InputRange from "react-input-range";
import "./priceRange.css";

class PriceSlider extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      min: this.props.priceRangeSliderRender.min,
      max: this.props.priceRangeSliderRender.max
    };
    this.handleMinValue = this.handleMinValue.bind(this);
    this.handleMaxValue = this.handleMaxValue.bind(this);
    console.log(this.props);
  }

  handleMinValue(event) {
    this.setState({ min: event.target.value });
  }
  handleMaxValue(event) {
    this.setState({ max: event.target.value });
  }

  componentDidUpdate() {
    if (this.props.priceRangeSliderRender.min !== this.state.min) {
      this.setState({
        min: this.props.priceRangeSliderRender.min,
        max: this.props.priceRangeSliderRender.max
      });
    }
  }

  render() {
    return (
      <div className="price-range">
        <h2>Price Range</h2>
        <div className="well price-range-box">
          <form className="form-inline">
            <input
              key={this.state.min}
              defaultValue={this.state.min}
              className="form-control"
              type="number"
            />
            <span>-</span>
            <input
              key={this.state.max}
              defaultValue={this.state.max}
              className="form-control"
              type="number"
            />
            <input type="submit" className="btn btn-primary" value="Go" />
          </form>
        </div>
      </div>
    );
  }
}
// {/**/}

export default PriceSlider;



