import React from "react";
import "./Spinner.css";

const Spinner = () => {
  return (
    <div className="spinnerWrapper">
      <div className="cssload-loader-inner">
        <div className="cssload-cssload-loader-line-wrap-wrap">
          <div className="cssload-loader-line-wrap" />
        </div>
        <div className="cssload-cssload-loader-line-wrap-wrap">
          <div className="cssload-loader-line-wrap" />
        </div>
        <div className="cssload-cssload-loader-line-wrap-wrap">
          <div className="cssload-loader-line-wrap" />
        </div>
        <div className="cssload-cssload-loader-line-wrap-wrap">
          <div className="cssload-loader-line-wrap" />
        </div>
        <div className="cssload-cssload-loader-line-wrap-wrap">
          <div className="cssload-loader-line-wrap" />
        </div>
      </div>
    </div>
  );
};

export default Spinner;



