import React, { Component } from 'react';
import CustomAutosuggest from '../customAutosuggest/CustomAutosuggest';
import VoiceSearch from '../voiceSearch/voiceSearch';

class AppHeader extends Component {  
  render() {    
    return (
      <header id="header">
         <div className="header_top">
           <div className="container">
             <div className="row">
               <div className="col-sm-8 ">
                 <div className="contactinfo">
                   <ul className="nav nav-pills">                 
                     <li><a href=""><i className="fa fa-envelope"></i> info@seeknshop.io</a></li>
                     <li><a href=""><i className="fa fa-user"></i> Account</a></li>
                     <li><a href=""><i className="fa fa-star"></i> Wishlist</a></li>
                     <li><a href="checkout.html"><i className="fa fa-crosshairs"></i> Checkout</a></li>
                     <li><a href="login.html"><i className="fa fa-lock"></i> Login</a></li>
                   </ul>
                 </div>
               </div>
               <div className="col-sm-4">
                 <div className="social-icons pull-right">
                   <ul className="nav navbar-nav">
                     <li><a href=""><i className="fa fa-facebook"></i></a></li>
                     <li><a href=""><i className="fa fa-twitter"></i></a></li>
                     <li><a href=""><i className="fa fa-linkedin"></i></a></li>
                     <li><a href=""><i className="fa fa-dribbble"></i></a></li>
                     <li><a href=""><i className="fa fa-google-plus"></i></a></li>
                   </ul>
                 </div>
               </div>
             </div>
           </div>
         </div>

         <div className="header-middle">
           <div className="container">
             <div className="row">
               <div className="col-sm-3">
                 <div className="logo pull-left">
                   <a href="index.html" className="sns-logo">SEEK<span>N</span>SHOP.IO</a>
                 </div>
               </div>
               <div className="col-sm-7">
                 <div className="input-group">
                   <div id="autoSuggest">
                      <CustomAutosuggest headerclearsearch={this.props.headerclearsearchitem} 
                                         onItemSelected={this.props.onAutoSuggestSelected} 
                                         searchboxvalueonheader={this.props.searchboxvalue} 
                                         suggesthistoryappheader={this.props.suggesthistoryapp} 
                                         fatchResultForHistory={this.props.fatchResultForHistory} 
                                         currentQueryApp={this.props.currentQueryApp}
                                         removeItemFromHistory={this.props.removeItemFromHistory}
                                         productsHead = {this.props.productsApp}
                                         clearHistoryData = {this.props.clearHistoryData} />
                   </div>
                  </div>
                  <VoiceSearch onVoiceSearchInitiated={this.props.onVoiceSearchSelected}
                               stringToRead={this.props.stringToRead}></VoiceSearch>
               </div>
               <div className="col-sm-2">
                 <a className="sns-cart" href="cart.html"><i className="fa fa-shopping-cart"></i> Cart</a>
               </div>
             </div>
           </div>
         </div>
         <div className="output"></div>
       </header>
    );
  }
}

export default AppHeader;



