import React, { Component } from 'react';

class Footer extends Component {
  render() {
    return (
      <footer id="footer">      		
      		<div className="footer-bottom">
      			<div className="container">
      				<div className="row">
      					<p className="pull-left">Copyright © 2017 seek. All rights reserved.</p>
      					<p className="pull-right">Designed by <span><a target="_blank" href="/">SNS</a></span></p>
      				</div>
      			</div>
      		</div>

      	</footer>
    );
  }
}

export default Footer;



