import React, { Component } from "react";
import "./App.css";
import Header from "./components/header/Header";
import LeftNav from "./components/LeftNav/LeftNav";
import ProductList from "./components/productList/ProductList";
import Footer from "./components/footer/Footer";
import Spinner from "./components/spinner/Spinner";
import $ from "jquery";

const rootURL = "http://52.163.118.40/cars/automate/";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      leftNavData: [],
      suggestedItem: [],
      currentPage: 1,
      visibleLoader: true,
      priceRange: {},
      filterArray: [],
      removeFilterItem: {},
      isLoading: false,
      suggestionTitle: "",
      filterCheckStatus:[],
      hasError:false,
      suggestHistory:[],
      currentQuery:"",
      notFromLeftNav:true,
      visibleAfterSearch: false,
      stringToRead:""
    };
    this.onAutoSuggestSelected = this.onAutoSuggestSelected.bind(this);
    this.getFilterItems = this.getFilterItems.bind(this);
    this.setDataToAutoSuggestion = this.setDataToAutoSuggestion.bind(this);
    this.logData = this.logData.bind(this);
    this.logDataForAutoSuggest = this.logDataForAutoSuggest.bind(this);
    this.getFilterItemsFromFacets = this.getFilterItemsFromFacets.bind(this);
    this.onVoiceSearchSelected = this.onVoiceSearchSelected.bind(this);
    this.makeApiCallWithParameter = this.makeApiCallWithParameter.bind(this);  
    this.appclearsearchitem = this.appclearsearchitem.bind(this);    
    this.removeItemFromHistory = this.removeItemFromHistory.bind(this);
    this.setCurrentSearchItem = this.setCurrentSearchItem.bind(this);
    this.clearHistoryData = this.clearHistoryData.bind(this);
  }
  makeApiCallWithParameter = (params, callback) => {
    var self = this;
    let defaultParams = {
      
    };  
    
    let param = params;
    
    $.ajax({
      url:rootURL,
      data:$.param(param),
      method:'GET',
      content:'json',
      success:function(data){
        var data = JSON.parse(data);
        var dataN = data.hits.hits,
          dataL = data.aggregations;
      console.log(dataN);
        if(dataN && dataL){  
          console.log(dataN);   
          callback(dataN, dataL);   
        }else{
          console.log("Oops no data found");
          self.setState({ isLoading: false, hasError:true });
          setTimeout(function(){
            self.setState({ hasError:false });
          },5000)
      }
      },
      error:function(err){
        self.setState({ isLoading: false, hasError:true });
        setTimeout(function(){
          self.setState({ hasError:false });
        },5000)
      }
    });
  
  }
 
  fetchSearchResult(prm, sugg, callback) {    

    let queryParamIs;
      if(sugg === ""){
        queryParamIs = prm
      }else if(prm === ""){
        queryParamIs = sugg
      }else{
        queryParamIs = prm + " " + sugg
      }
      let params = {
        query: queryParamIs
      };
    this.makeApiCallWithParameter(params, callback);
  }

  fetchCategoryData(category, callback) {
    let params = {
      categoryId: category
    };
    this.makeApiCallWithParameter(params, callback);
  }
  setDataToAutoSuggestion(dataN, dataL) {   
          this.setState(
            { products: dataN, keySearch: [], leftNavData: dataL, isLoading: false },
            function() {
              var pData = this.state.products,
                maxPrice = Math.max.apply(
                  Math,
                  this.state.products.map(function(o) {
                    return o._source.price;
                  })
                ),
                minPrice = Math.min.apply(
                  Math,
                  this.state.products.map(function(o) {
                    return o._source.price;
                  })
                );

              this.setState(
                {
                  suggestedItem: pData,
                  priceRange: { min: minPrice, max: maxPrice },
                  visibleLoader: !this.state.visibleLoader
                },
                function() {
                  console.log(this.state.priceRange);
                }
              );
            }
          );       
  }
  logData(dataN, dataL) {
    this.setState({ suggestedItem: dataN, keySearch: [], isLoading: false });
  }
  logDataForAutoSuggest(dataN, dataL) {
    var stringToRead="", count = 1;
    this.setState({
      suggestedItem: dataN,
      keySearch: [],
      leftNavData: dataL,
      isLoading: false,
      visibleAfterSearch: true
    },function(){
      this.state.suggestedItem.map((item, index)=>{
          if(index < 5){
            stringToRead+= 'Product ' + count++ + ": "+ item._source.title + ", Price: Rupees " + item._source.price + ". "
          }else{
            return
          }          
      });
      this.setState({stringToRead:stringToRead})
      console.log(stringToRead);
    });
  }
  componentWillMount() {    
    this.fetchSearchResult("all", "", this.setDataToAutoSuggestion);
  }
  getFilterItems(action) {
    console.log(action);
    this.setState({ notFromLeftNav:false });
    let itemToReset = {};
    if (action.to) {
      this.state.filterArray.push(action.item);
      /* clear flage status  */
      this.setState({ removeFilterItem: [] });
    } else {
      let index = this.state.filterArray.indexOf(action.item);
      if (index > -1) {
        this.state.filterArray.splice(index, 1);
      }
      this.setState({ removeFilterItem: itemToReset });
    }
    var queryString = this.state.filterArray.join(" ") || "all";
    console.log(queryString);
    if (this.state.suggestionTitle) {
      if(queryString === ""){
        queryString = this.state.suggestionTitle;
      }else{
        queryString += " " + this.state.suggestionTitle;
      }     
    }
    
    this.setState({ isLoading: true, notFromLeftNav:true });
    this.fetchSearchResult(queryString, "", this.logData);
   
  }
  getFilterItemsFromFacets(action) { 
    console.log(action);
    this.setState({ notFromLeftNav:false });
    let itemToReset = {};
      if (action.to) {
        this.state.filterArray.push(action.item);
      } else {
        let index = this.state.filterArray.indexOf(action.item);
        if (index > -1) {
          this.state.filterArray.splice(index, 1);
        } 

        itemToReset.item = action.item;
        itemToReset.checkState = false;
        this.setState({ removeFilterItem: itemToReset });
      }
      var queryString = this.state.filterArray.join(" ") || "all";
      console.log(queryString);
      if (this.state.suggestionTitle) {
        if(queryString === ""){
          queryString = this.state.suggestionTitle;
        }else{
          queryString += " " + this.state.suggestionTitle;
        }        
      }
     this.setState({ isLoading: true, notFromLeftNav: true });
     this.fetchSearchResult(queryString,"",this.logData);
    
  }
  onAutoSuggestSelected(event, suggestions) {
    //this.setState()
    var { enteredValue, suggestion: { title } } = suggestions;
    console.log(title);
    if(title !== "" || enteredValue !== ""){
      this.setCurrentSearchItem(title);    

      this.setState({ isLoading: true, suggestionTitle: title});
      this.fetchSearchResult(title, enteredValue, this.logDataForAutoSuggest);
    }
   
  }

  onVoiceSearchSelected(suggestions) {
    //this.setState()
    var enteredValue = "";
    var title = suggestions;
    console.log(title);
    this.setState({ isLoading: true, suggestionTitle: title });
    document.getElementById('autoSearchBox').value = suggestions;
    this.setCurrentSearchItem(title);

    this.fetchSearchResult(title, enteredValue, this.logDataForAutoSuggest);
    setTimeout(function(){
      document.getElementById('autoSearchBox').value = suggestions;
    },2000);
  }
  
  appclearsearchitem() {        
    this.setState({suggestionTitle:""});
  }

  setCurrentSearchItem = (title) => {
    if(!this.state.suggestHistory.includes(title) && this.state.suggestHistory){
      this.state.suggestHistory.push(title);
    }      

    this.setState({ currentQuery:title });
  }

  fatchResultForHistoryApp = (item) => {    
     var enteredValue = "";
     var title = item.item;
     console.log(title);
     this.setState({ isLoading: true, suggestionTitle: title });   
     document.getElementById('historySearchBox').className="hidden";
     this.setCurrentSearchItem(title);
     this.fetchSearchResult(title, enteredValue, this.logDataForAutoSuggest);    
  }
  removeItemFromHistory = (historyItem) => {   
    let historyArray = this.state.suggestHistory,
    index = historyArray.indexOf(historyItem);
    if (index > -1) {
      historyArray.splice(index, 1);
    } 
    this.setState({suggestHistory:historyArray,currentQuery:""});   
  }
  clearHistoryData () { 
    this.setState({suggestHistory:[],currentQuery:""});
  }
   render() {
    return (
      <div>
        <Header
          key={this.onAutoSuggestSelected.pid}
          onAutoSuggestSelected={this.onAutoSuggestSelected}
          onVoiceSearchSelected={this.onVoiceSearchSelected}
          searchboxvalue={this.state.suggestionTitle}
          headerclearsearchitem={this.appclearsearchitem}
          suggesthistoryapp={this.state.suggestHistory}
          fatchResultForHistory={this.fatchResultForHistoryApp}
          currentQueryApp={this.state.currentQuery}
          removeItemFromHistory = {this.removeItemFromHistory}
          productsApp = {this.state.products}
          clearHistoryData = {this.clearHistoryData}
          stringToRead={this.state.stringToRead}
        />
       
        <section>
          <div className="container">
            <div className="row">
            <div className="warning"
             className={
              this.state.hasError
                ? "warning"
                : "hidden"
            }
            >Oops, no result found for your query, please search again</div>
              <div
                className={
                  this.state.isLoading
                    ? "spinnerPlaceholder"
                    : "spinnerPlaceholder hidden"
                }
              >
                <Spinner />
              </div>
              <div className={this.state.visibleAfterSearch ? "hidden" : ""}>
                  <div className="row gray-ph-box">
                    <div className="col-sm-3"><span></span></div>
                    <div className="col-sm-9"><span></span></div>
                  </div>
                </div>
              <div className={this.state.visibleAfterSearch ? "" : "hidden"}>
              <LeftNav
                key={this.state.leftNavData.name}
                selectedFilters={this.state.filterArray}
                showLeftNave={this.state.leftNavData}
                priceRangeSlider={this.state.priceRange}
                visibleLoader={this.state.visibleLoader}
                getCategoryItemApp={this.getFilterItems}
                getFilterItemsFromFacets={this.getFilterItemsFromFacets}
                removeFilterInLN={this.state.removeFilterItem}
                filterCheckStatusForLN={this.state.filterCheckStatus}
              />
              <ProductList
                products={this.state.suggestedItem}
                todosPerPage={5}
                visibleLoader={this.state.visibleLoader}
              />
              </div>
            </div>
          </div>
        </section>
        <div className={this.state.visibleAfterSearch ? "" : "fix-to-bottom"}>
        <Footer />
        </div>
      </div>
    );
  }
}

export default App;



